@charset "UTF-8";
:root {
  --primary-orange: #FA8543;
  --primary-brown: #C3BAAC;
  --primary-yellow: #FFDC00;
  --black-light: #1A1A1A;
  --black-lighten: #192033;
  --black-normal: #010101;
  --black-dark: #000000;
  --white: #FFFFFF;
  --white-normal: #F5F5F5;
  --white-dark: #D9D9D9;
  --gray-900: #030E15;
  --gray-800: #2D3235;
  --gray-700: #4F5152;
  --gray-600: #78797A;
  --gray-500: #9A9D9E;
  --gray-400: #BABCBD;
  --gray-300: #C6C7C8;
  --gray-200: #DADCDD;
  --gray-100: #F6F6F6;
  --swiper-grey: #d3d3d3;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  list-style: none;
  text-decoration: none;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  overflow-x: hidden !important;
}

body {
  -webkit-font-smoothing: antialiased;
}

main {
  position: relative !important;
}

a {
  display: inline-block;
}

svg, img {
  display: inline-block;
  max-width: 100%;
}

img {
  -o-object-fit: cover;
  object-fit: cover;
}

button, a {
  background-color: transparent;
  border: none;
  cursor: pointer !important;
}

button {
  cursor: pointer;
}

:focus {
  outline: 0;
  box-shadow: 0 0 0 2px var(--primary-green);
}

.fancybox__container {
  z-index: 999999 !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 1400px) {
  .container {
    padding: 0px 25px;
  }
}
@media (max-width: 678px) {
  .container {
    padding: 0px 15px;
  }
}
h1, h2, h3, h4, h5, h6, p, span, a {
  font-family: "Roboto", sans-serif;
}

h2, h3 {
  line-height: 120%;
}

h2, h5, h6, p {
  line-height: 130%;
}

h1 {
  font-size: 96px;
  font-weight: 800;
  line-height: 100%;
  letter-spacing: -0.96px;
}

h2 {
  font-size: 40px;
  font-weight: 800;
  letter-spacing: -0.8px;
}

h3 {
  font-size: 36px;
  font-weight: 800;
  letter-spacing: -0.72px;
}

h4 {
  font-size: 28px;
  font-weight: 600;
}

h5 {
  font-size: 20px;
  font-weight: 700;
}

h6 {
  font-size: 18px;
  font-weight: 500;
}

p {
  color: var(--black-dark);
  font-size: 18px;
  font-weight: 400;
}

header {
  background-color: #0B0A0D;
  height: 18vh;
}
header .custom-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
header .custom-header a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header .custom-header a img {
  width: 6rem;
  height: auto;
}
header .custom-header a p {
  color: white;
  font-weight: bold;
  font-size: 0.6rem;
  margin: 0;
}
header .custom-header nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 40px;
}
header .custom-header nav ul li {
  margin-left: 1rem;
}
header .custom-header nav ul li a {
  font-weight: bold;
  color: white;
  text-decoration: none;
}

section {
  width: 100%;
  height: calc(100vh - 9.6rem);
}

#s__home {
  background-color: red;
  background-image: url("../assets/bg/bg.jpg");
  background-size: cover;
}
#s__home .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  color: white;
}
#s__home .container h1 {
  color: white !important;
}
#s__home .container .filter {
  background-color: rgba(0, 0, 0, 0.5);
  width: 70rem;
  padding: 1rem;
  border: 1px solid red;
}
#s__home .container .filter p {
  margin-bottom: 1rem;
}
#s__home .container .filter form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#s__home .container .filter form .filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem; /* Adiciona espa√ßo entre os itens */
}
#s__home .container .filter form .filters .filter-item {
  width: 10rem;
}
#s__home .container .filter form .filters .filter-item label {
  display: block;
  margin-bottom: 0.5rem;
}
#s__home .container .filter form .filters .filter-item input,
#s__home .container .filter form .filters .filter-item select {
  width: 100%;
}

#s__imoveis {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
#s__imoveis .container .row .card {
  width: 250px;
  height: 250px;
}